<template><section><h1>描述</h1>
<p>使用指令可以快速切换元素的disable状态，为元素添加类，并且禁用元素的点击事件</p>
<h1>效果展示</h1>
<template>
<div>
  <input type="text" style="border:1px solid black" v-bs-disable="dis">
  <v-btn v-bs-disable="dis" @click="clicks">click</v-btn>
  <v-btn @click="dis=!dis">Change</v-btn>
  {{msg}}
  <div>disabled: {{dis}}</div>
</div>
</template>

<h1>代码示例</h1>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"border:1px solid black"</span> <span class="hljs-attr">v-bs-disable</span>=<span class="hljs-string">"dis"</span> /&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">v-bs-disable</span>=<span class="hljs-string">"dis"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicks"</span>&gt;</span>click<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dis=!dis"</span>&gt;</span>Change<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
  {{msg}}
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>disabled: {{dis}}<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">dis</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">msg</span>: <span class="hljs-string">''</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    clicks() {
      <span class="hljs-keyword">this</span>.msg = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>必须</td>
</tr>
</tbody>
</table>
<h1>样式定义</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>类名</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>.bs-disabled</td>
<td>禁用时添加</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      dis: false,
      msg: ''
    }
  },
  methods: {
    clicks() {
      this.msg = new Date()
    }
  }
}
</script>


